import Vue from 'vue'
import Vuex from 'vuex';

Vue.use(Vuex)

const store=new Vuex.Store({

  state:{
    uesr:{}
  },
 getters:{
getusername:state => {
  return state.uesr.name;
},
   getuser:state => {
     return state.uesr;
   }
 },
 mutations:{
    adduser(state,payload){
      state.uesr=payload;
    }

 },
 actions:{

 }

})

export default store



